<template>
  <div class="recordadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <table class="table table-bordered table-sm bg-light">
              <tr>
                <td class="form_title align-middle" colspan="4">評估日期</td>
                <td>
                  <v-text-field type="date" v-model="list.date" :rules="emptyRules"></v-text-field>
                </td>
              </tr>
              <!-- 意識狀態 -->
              <tr>
                <td class="form_title" rowspan="4">
                  <span class="vertical-mode">意識狀態</span>
                </td>
                <td>意識程度</td>
                <td colspan="2">1.清醒 2.混亂 3.瞻妄 4.嗜睡 5.木僵 6.植物人 7.昏迷 8.其他</td>
                <td>
                  <select
                    class="form-control"
                    id="ideology"
                    name="ideology"
                    v-model="list.ideology"
                  >
                    <option>1.清醒</option>
                    <option>2.混亂</option>
                    <option>3.瞻妄</option>
                    <option>4.嗜睡</option>
                    <option>5.木僵</option>
                    <option>6.植物人</option>
                    <option>7.昏迷</option>
                    <option>8.其他</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td rowspan="3">GCS</td>
                <td>E 睜眼反應 eye open</td>
                <td>
                  1.對任何刺激均無反應
                  <br />2.疼痛刺激張開眼睛
                  <br />3.聲音刺激張開眼睛
                  <br />4.自動睜開
                </td>
                <td>
                  <select class="form-control" id="e" name="e" v-model="list.e">
                    <option>1.對任何刺激均無反應</option>
                    <option>2.疼痛刺激張開眼睛</option>
                    <option>3.聲音刺激張開眼睛</option>
                    <option>4.自動睜開</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>M 最佳活動反應 best motor response</td>
                <td>
                  1.疼痛刺激沒有反應
                  <br />2.強直性伸張狀
                  <br />3.異常屈曲
                  <br />4.退縮
                  <br />5.能辨識痛點
                  <br />6.依照指令動作
                </td>
                <td>
                  <select class="form-control" id="m" name="m" v-model="list.m">
                    <option>1.疼痛刺激沒有反應</option>
                    <option>2.強直性伸張狀</option>
                    <option>3.異常屈曲</option>
                    <option>4.退縮</option>
                    <option>5.能辨識痛點</option>
                    <option>6.依照指令動作</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>V 最佳語言反應 best verbal response</td>
                <td>
                  1.疼痛刺激無法引起反應
                  <br />2.喃喃且無法理解的聲音
                  <br />3.嗜睡
                  <br />4.混亂
                  <br />5.清醒
                  <br />A.失語
                  <br />T.氣切
                </td>
                <td>
                  <select class="form-control" id="v" name="v" v-model="list.v">
                    <option>1.疼痛刺激無法引起反應</option>
                    <option>2.喃喃且無法理解的聲音</option>
                    <option>3.嗜睡</option>
                    <option>4.混亂</option>
                    <option>5.清醒</option>
                    <option>A.失語</option>
                    <option>T.氣切</option>
                  </select>
                </td>
              </tr>

              <!-- 心肺功能 -->
              <tr>
                <td class="form_title" rowspan="4">
                  <span class="vertical-mode">心肺功能</span>
                </td>
                <td>心律</td>
                <td colspan="2">1.規則 2.不規則</td>
                <td>
                  <select
                    class="form-control"
                    id="heart_rate"
                    name="heart_rate"
                    v-model="list.heart_rate"
                  >
                    <option>1.規則</option>
                    <option>2.不規則</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>呼吸型態</td>
                <td colspan="2">1.正常 2.端坐 3.呼吸器輔助 4.氧氣使用:____升/分 5.其他</td>
                <td>
                  <select
                    class="form-control"
                    id="breath_state"
                    name="breath_state"
                    v-model="list.breath_state"
                  >
                    <option>1.正常</option>
                    <option>2.端坐</option>
                    <option>3.呼吸器輔助</option>
                    <option>4.氧氣使用</option>
                    <option>5.其他</option>
                  </select>
                  <input
                    v-if="list.breath_state == '4.氧氣使用'"
                    type="text"
                    id="breath_state_content"
                    name="breath_state_content"
                    class="form-control"
                    placeholder="升/分"
                    v-model="list.breath_state_content"
                  />
                  <input
                    v-if="list.breath_state == '5.其他'"
                    type="text"
                    id="breath_state_content"
                    name="breath_state_content"
                    class="form-control"
                    placeholder="內容"
                    v-model="list.breath_state_content"
                  />
                </td>
              </tr>
              <tr>
                <td>呼吸音</td>
                <td colspan="2">N:正常 W:喘鳴 Ra:囉聲 Rh:乾囉音 C:爆裂音</td>
                <td>
                  <select
                    class="form-control"
                    id="breath_sound"
                    name="breath_sound"
                    v-model="list.breath_sound"
                  >
                    <option>N:正常</option>
                    <option>W:喘鳴</option>
                    <option>Ra:囉聲</option>
                    <option>Rh:乾囉音</option>
                    <option>C:爆裂音</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>痰</td>
                <td colspan="2">0.無 1.自咳 2.抽痰(顏色/量)</td>
                <td>
                  <select class="form-control" id="sputum" name="sputum" v-model="list.sputum">
                    <option>0.無</option>
                    <option>1.自咳</option>
                    <option>2.抽痰(顏色/量)</option>
                  </select>
                </td>
              </tr>
              <!-- 口腔吞嚥 -->
              <tr>
                <td class="form_title" rowspan="3">
                  <span class="vertical-mode">口腔吞嚥</span>
                </td>
                <td>黏膜</td>
                <td colspan="2">0.完整 1.破損</td>
                <td>
                  <select class="form-control" id="mucosa" name="mucosa" v-model="list.mucosa">
                    <option>0.完整</option>
                    <option>1.破損</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>咬合</td>
                <td colspan="2">0.不影響咀嚼 1.會影響咀嚼 2.不由口進食</td>
                <td>
                  <select
                    class="form-control"
                    id="occlusal"
                    name="occlusal"
                    v-model="list.occlusal"
                  >
                    <option>0.不影響咀嚼</option>
                    <option>1.會影響咀嚼</option>
                    <option>2.不由口進食</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>吞嚥能力</td>
                <td colspan="2">0.平順 1.偶嗆(1-3次) 2.常嗆食(3次以上) 3.無吞嚥能力 4.無法得知</td>
                <td>
                  <select
                    class="form-control"
                    id="swallowing"
                    name="swallowing"
                    v-model="list.swallowing"
                  >
                    <option>0.平順</option>
                    <option>1.偶嗆(1-3次)</option>
                    <option>2.常嗆食(3次以上)</option>
                    <option>3.無吞嚥能力</option>
                    <option>4.無法得知</option>
                  </select>
                </td>
              </tr>
              <!-- 排泄功能 -->
              <tr>
                <td class="form_title" rowspan="6">
                  <span class="vertical-mode">排泄功能</span>
                </td>
                <td>小便性質</td>
                <td colspan="2">0.清澈 1.混濁 2.有沉澱物</td>
                <td>
                  <select class="form-control" id="pee" name="pee" v-model="list.pee">
                    <option>0.清澈</option>
                    <option>1.混濁</option>
                    <option>2.有沉澱物</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>排尿方式</td>
                <td colspan="2">0.至廁所或使用尿壼(便盆椅) 1.尿布 2.尿套 3.間歇性導尿 4.存留導尿管</td>
                <td>
                  <select
                    class="form-control"
                    id="urination"
                    name="urination"
                    v-model="list.urination"
                  >
                    <option>0.至廁所或使用尿壼(便盆椅)</option>
                    <option>1.尿布</option>
                    <option>2.尿套</option>
                    <option>3.間歇性導尿</option>
                    <option>4.存留導尿管</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td rowspan="3">腸蠕動</td>
                <td>腸音</td>
                <td>0.正常 1.過慢 2.過速 3.無</td>
                <td>
                  <select
                    class="form-control"
                    id="intestinal_peristalsis"
                    name="intestinal_peristalsis"
                    v-model="list.intestinal_peristalsis"
                  >
                    <option>0.正常</option>
                    <option>1.過慢</option>
                    <option>2.過速</option>
                    <option>3.無</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>脹氣</td>
                <td>0.無 1.有</td>
                <td>
                  <select
                    class="form-control"
                    id="flatulence"
                    name="flatulence"
                    v-model="list.flatulence"
                  >
                    <option>0.無</option>
                    <option>1.有</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>硬塊</td>
                <td>0.無 1.有(註明部位)</td>
                <td>
                  <select
                    class="form-control"
                    id="hard_block"
                    name="hard_block"
                    v-model="list.hard_block"
                  >
                    <option>0.無</option>
                    <option>1.有(註明部位)</option>
                  </select>
                  <input
                    v-if="list.hard_block == '1.有(註明部位)'"
                    type="text"
                    id="hard_block_content"
                    name="hard_block_content"
                    class="form-control"
                    placeholder="內容"
                    v-model="list.hard_block_content"
                  />
                </td>
              </tr>
              <tr>
                <td>排便處理</td>
                <td colspan="2">0.正常 1.軟便劑 2.輕瀉劑 3.灌腸 4.挖便 5.造瘻口 6.其他</td>
                <td>
                  <select
                    class="form-control"
                    id="defecation_treatment"
                    name="defecation_treatment"
                    v-model="list.defecation_treatment"
                  >
                    <option>0.正常</option>
                    <option>1.軟便劑</option>
                    <option>2.輕瀉劑</option>
                    <option>3.灌腸</option>
                    <option>4.挖便</option>
                    <option>5.造瘻口</option>
                    <option>6.其他</option>
                  </select>
                </td>
              </tr>
              <!-- 皮膚狀態 -->
              <tr>
                <td class="form_title" rowspan="3">
                  <span class="vertical-mode">皮膚狀態</span>
                </td>
                <td>外觀</td>
                <td colspan="2">A.正常 B.鬆弛 C.乾燥 D.搔癢 E.水腫 F.瘀血 G.疹子 H.腫塊 I.水泡</td>
                <td>
                  <select
                    class="form-control"
                    id="skin_exterior"
                    name="skin_exterior"
                    v-model="list.skin_exterior"
                  >
                    <option>A.正常</option>
                    <option>B.鬆弛</option>
                    <option>C.乾燥</option>
                    <option>D.搔癢</option>
                    <option>E.水腫</option>
                    <option>F.瘀血</option>
                    <option>G.疹子</option>
                    <option>H.腫塊</option>
                    <option>I.水泡</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>完整性</td>
                <td colspan="2">0.完整 1.破損(請填寫部位)</td>
                <td>
                  <select
                    class="form-control"
                    id="skin_complete"
                    name="skin_complete"
                    v-model="list.skin_complete"
                  >
                    <option>0.完整</option>
                    <option>1.破損(請填寫部位)</option>
                  </select>
                  <input
                    v-if="list.skin_complete == '1.破損(請填寫部位)'"
                    type="text"
                    id="skin_complete_content"
                    name="skin_complete_content"
                    class="form-control"
                    placeholder="內容"
                    v-model="list.skin_complete_content"
                  />
                </td>
              </tr>
              <tr>
                <td>肢體水腫</td>
                <td colspan="2">
                  0.無水腫 1.水腫程度:1+ 2+ 3+ 4+
                  <br />
                  <span class="small">
                    +:凹陷 2mm,但很快恢復,且下肢看不到水腫
                    <br />++:中度凹陷 4mm,但10~15秒後才恢復
                    <br />+++:深度凹陷 6mm,持續較久的時間才恢復,下肢可見腫脹
                    <br />++++:嚴重凹陷 8mm,會凹陷一段時間,下肢已經非常腫脹
                  </span>
                </td>
                <td>
                  <table class="table">
                    <tr>
                      <td colspan="3">
                        右
                        <span class="float-right">左</span>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <select
                          class="form-control"
                          id="limb_edema1"
                          name="limb_edema1"
                          v-model="list.limb_edema1"
                        >
                          <option value="0">0.無水腫</option>
                          <option value="1+">1.水腫程度:1+</option>
                          <option value="2+">1.水腫程度:2+</option>
                          <option value="3+">1.水腫程度:3+</option>
                          <option value="4+">1.水腫程度:4+</option>
                        </select>
                      </td>
                      <td rowspan="2" style="width:100px">
                        <div class="d-flex justify-content-center">
                          <img :src="sman_pic" />
                        </div>
                      </td>
                      <td>
                        <select
                          class="form-control"
                          id="limb_edema2"
                          name="limb_edema2"
                          v-model="list.limb_edema2"
                        >
                          <option value="0">0.無水腫</option>
                          <option value="1+">1.水腫程度:1+</option>
                          <option value="2+">1.水腫程度:2+</option>
                          <option value="3+">1.水腫程度:3+</option>
                          <option value="4+">1.水腫程度:4+</option>
                        </select>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <select
                          class="form-control"
                          id="limb_edema3"
                          name="limb_edema3"
                          v-model="list.limb_edema3"
                        >
                          <option value="0">0.無水腫</option>
                          <option value="1+">1.水腫程度:1+</option>
                          <option value="2+">1.水腫程度:2+</option>
                          <option value="3+">1.水腫程度:3+</option>
                          <option value="4+">1.水腫程度:4+</option>
                        </select>
                      </td>
                      <td>
                        <select
                          class="form-control"
                          id="limb_edema4"
                          name="limb_edema4"
                          v-model="list.limb_edema4"
                        >
                          <option value="0">0.無水腫</option>
                          <option value="1+">1.水腫程度:1+</option>
                          <option value="2+">1.水腫程度:2+</option>
                          <option value="3+">1.水腫程度:3+</option>
                          <option value="4+">1.水腫程度:4+</option>
                        </select>
                      </td>
                    </tr>
                  </table>
                </td>
              </tr>
              <!-- 肢體活動 -->
              <tr>
                <td class="form_title" rowspan="5">
                  <span class="vertical-mode">肢體活動</span>
                </td>
                <td rowspan="3">平衡</td>
                <td>坐姿</td>
                <td rowspan="3">0.穩定 1.稍穩定 2.極不穩定 3.無法執行</td>
                <td>
                  <select
                    class="form-control"
                    id="balance1"
                    name="balance1"
                    v-model="list.balance1"
                  >
                    <option>0.穩定</option>
                    <option>1.稍穩定</option>
                    <option>2.極不穩定</option>
                    <option>3.無法執行</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>站姿</td>
                <td>
                  <select
                    class="form-control"
                    id="balance2"
                    name="balance2"
                    v-model="list.balance2"
                  >
                    <option>0.穩定</option>
                    <option>1.稍穩定</option>
                    <option>2.極不穩定</option>
                    <option>3.無法執行</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>步行</td>
                <td>
                  <select
                    class="form-control"
                    id="balance3"
                    name="balance3"
                    v-model="list.balance3"
                  >
                    <option>0.穩定</option>
                    <option>1.稍穩定</option>
                    <option>2.極不穩定</option>
                    <option>3.無法執行</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>關節</td>
                <td colspan="2">0.正常 1.受限 2.失能</td>
                <td>
                  <table class="table">
                    <tr>
                      <td colspan="3">
                        右
                        <span class="float-right">左</span>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <select
                          class="form-control"
                          id="joint1"
                          name="joint1"
                          v-model="list.joint1"
                        >
                          <option value="0">0.正常</option>
                          <option value="1">1.受限</option>
                          <option value="2">2.失能</option>
                        </select>
                      </td>
                      <td rowspan="2" style="width:100px">
                        <div class="d-flex justify-content-center">
                          <img :src="sman_pic" />
                        </div>
                      </td>
                      <td>
                        <select
                          class="form-control"
                          id="joint2"
                          name="joint2"
                          v-model="list.joint2"
                        >
                          <option value="0">0.正常</option>
                          <option value="1">1.受限</option>
                          <option value="2">2.失能</option>
                        </select>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <select
                          class="form-control"
                          id="joint3"
                          name="joint3"
                          v-model="list.joint3"
                        >
                          <option value="0">0.正常</option>
                          <option value="1">1.受限</option>
                          <option value="2">2.失能</option>
                        </select>
                      </td>
                      <td>
                        <select
                          class="form-control"
                          id="joint4"
                          name="joint4"
                          v-model="list.joint4"
                        >
                          <option value="0">0.正常</option>
                          <option value="1">1.受限</option>
                          <option value="2">2.失能</option>
                        </select>
                      </td>
                    </tr>
                  </table>
                </td>
              </tr>
              <tr>
                <td>肌力</td>
                <td colspan="2">5分:正常 4分:運動稍受阻力 3分:無法支重 2分:無重力時可作水平 1分:可見肌肉收縮 0分:完全無法動</td>
                <td>
                  <table class="table">
                    <tr>
                      <td colspan="3">
                        右
                        <span class="float-right">左</span>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <select
                          class="form-control"
                          id="muscle_strength1"
                          name="muscle_strength1"
                          v-model="list.muscle_strength1"
                        >
                          <option value="5">5分:正常</option>
                          <option value="4">4分:運動稍受阻力</option>
                          <option value="3">3分:無法支重</option>
                          <option value="2">2分:無重力時可作水平</option>
                          <option value="1">1分:可見肌肉收縮</option>
                          <option value="0">0分:完全無法動</option>
                        </select>
                      </td>
                      <td rowspan="2">
                        <img :src="sman_pic" />
                      </td>
                      <td>
                        <select
                          class="form-control"
                          id="muscle_strength2"
                          name="muscle_strength2"
                          v-model="list.muscle_strength2"
                        >
                          <option value="5">5分:正常</option>
                          <option value="4">4分:運動稍受阻力</option>
                          <option value="3">3分:無法支重</option>
                          <option value="2">2分:無重力時可作水平</option>
                          <option value="1">1分:可見肌肉收縮</option>
                          <option value="0">0分:完全無法動</option>
                        </select>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <select
                          class="form-control"
